<template>
    <div class="button-wrapper">
        <button
                class="button"
                type="button"
                :class="computedClass"
                @click="click"
        >
            {{text}}
        </button>
    </div>
</template>

<script>
export default {
    name: 'Button',
    props: {
        text: {
            type: String,
            default: null,
            required: true,
        },
        borderColor: {
            type: String,
            default: 'white',
            required: true,
        },
        color: {
            type: String,
            default: null,
            required: false,
        },
    },
    computed: {
        computedClass() {
            let classText = `border-${this.borderColor}`;

            if (this.color !== null)
            {
                classText += ` ${this.color}`;
            }

            return classText;
        },
    },
    methods: {
        click() {
            this.$emit('click');
        },
    },
};
</script>

