
















































import { Component, Prop, Vue } from 'vue-property-decorator';
import Button from './input/Button.vue';
import { getAuthService } from '../services/authService';

@Component({
    name: 'MainPageHeader',
    components: {
        Button,
    },
})
export default class MainPageHeader extends Vue {
    navbarBurgerExpanded: boolean = false;
    loggedInEmail: string|null = null;
    role: number|null = null;

    menuLinks: [string, string][] = [
        ['/form', 'Sample'],
    ]

    get loggedIn() {
        return this.loggedInEmail != null;
    }

    expandNavbarBurger() {
        this.navbarBurgerExpanded = !this.navbarBurgerExpanded;
    }

    signUp() {
        this.$router.push({ name: 'signUpPage' });
    }

    login() {
        this.$router.push({ name: 'logInPage', params: { tabKey: this.$route.params.tabKey } });
    }

    async logout() {
        await getAuthService().logOut();
        // Refreshing the page
        this.$router.go(0);
    }

    async mounted() {
        const authService = getAuthService();
        
        [
            this.loggedInEmail,
            this.role,
        ] = await Promise.all([
            authService.getLoggedInEmail(),
            authService.getRole(),
        ]);
    }
}
