









































import { Component, Prop, Vue } from 'vue-property-decorator';
import MainPageHeader from '../components/MainPageHeader.vue';
import Footer from '../components/Footer.vue';
import Tablet from '../components/Tablet.vue';
import WaveEffect from '../components/WaveEffect.vue';

const sampleText: string = `Lorem ipsum dolor sit amet, consectetur adipiscing elit.
        Curabitur et augue in nunc suscipit efficitur. Quisque quis justo neque.
        Nam iaculis felis quis feugiat egestas. Integer tempor dolor et ultricies dapibus.
        Vivamus aliquam et velit quis blandit. Maecenas malesuada magna nec dui tempor,
        ut sollicitudin sem hendrerit.
        Nulla id efficitur orci. Nunc quis laoreet est, ut gravida velit erat curae.`;

@Component({
    name: 'MainPage',
    components: {
        MainPageHeader,
        Footer,
        Tablet,
        WaveEffect,
    },
})
export default class MainPage extends Vue {
    currentSet: number = 0;
    pointerVisible: boolean = false;
    pointerShowTimeout: number = 3000;
    tabletsClean: boolean = false;
    waveEffect: boolean = false;

    pointerTimeout: number = 0;

    tabletTexts: { left: string, middle: string, right: string }[] = [
        {
            left: sampleText,
            middle: sampleText,
            right: sampleText,
        }, {
            left: sampleText.substring(50),
            middle: sampleText.substring(70),
            right: sampleText.substring(20),
        }, {
            left: sampleText.substring(100),
            middle: sampleText.substring(30),
            right: sampleText.substring(50),
        }, {
            left: sampleText.substring(150),
            middle: sampleText.substring(70),
            right: sampleText.substring(100),
        }, {
            left: sampleText.substring(20),
            middle: sampleText.substring(50),
            right: sampleText.substring(70),
        },
    ];

    clickPointer(): void {
        this.tabletsClean = true;
        this.waveEffect = true;
        this.pointerTimeout = setTimeout(() => this.hidePointer(), 1000);
    }

    hidePointer(): void {
        this.tabletsClean = false;
        this.waveEffect = false;

        this.currentSet = (this.currentSet + 1) % this.tabletTexts.length;

        this.pointerVisible = false;
        this.pointerTimeout = setTimeout(() => this.showPointer(), 2000);
    }

    showPointer(): void {
        this.pointerVisible = true;
        this.pointerTimeout = setTimeout(() => this.clickPointer(), 2300);
    }

    mounted() {
        this.pointerTimeout = setTimeout(() => this.showPointer(), 2000);
    }

    unmounted() {
        clearTimeout(this.pointerTimeout);
    }
}
